<template lang="pug">
p-slide
  template(#header)
    h2.my-0 {{resume.title}}
  info-section(v-if="resume.education.list" :name="resume.education.title" icon="fas fa-graduation-cap")
    .d-flex.flex-wrap.flex-column.flex-md-row
      info-card(v-for="ed in resume.education.list" :title="ed.title" 
        :sub-title="ed.period")
        b-card-text
          div {{ed.prof}}
          div {{ed.degree}}
  info-section(v-if="resume.experience.list" :name="resume.experience.title" icon="fas fa-briefcase")
    .d-flex.flex-wrap.flex-column.flex-md-row
      info-card(v-for="exp in resume.experience.list" :title="exp.title" 
        :sub-title="exp.period")
        b-card-text
          div {{exp.position}}
          div {{exp.desc}}
  info-section(v-if="resume.skills.list" :name="resume.skills.title" icon="fas fa-cogs")
    .d-flex.flex-wrap.flex-column.flex-md-row
      info-card(v-for="(sg,i) in resume.skills.list" :key="i" :title="sg.group")
        progress-item(v-for="(el,i) in sg.list" :name="el.name"
          :level="el.level" :key="i")
          
      
</template>
<script>
import { resume } from '../locale.js';
import PSlide from './Slide.vue';
import InfoSection from './InfoSection.vue';
import InfoCard from './InfoCard.vue';
import ProgressItem from './ProgressItem.vue';

export default {
    data(){
      return{
        resume: resume
        
      }
    },
    components:{
      PSlide,
      InfoSection,
      InfoCard,
      ProgressItem
    }
}
</script>
