<template lang="pug">
#app.h-100.themer
  #appWrapper.h-100.w-100.p-0.d-flex.flex-column
    b-navbar#pageHeadNav(tag="header" type="dark").w-100.theme-bg
      b-container(fluid).navCont.px-sm-4.px-md-4.px-lg-4
        //b-navbar-toggle(target="head-nav").ml-auto
        b-collapse#head-nav(is-nav)
          b-navbar-nav.mx-auto
            b-nav-item(v-for="slide in slides" :href="'#'+slide" @click="setSlide(slide)").header-btn.mx-2
              span.text-white {{slide | capitalize}}
            //b-nav-item-dropdown(text="Lang").header-btn
              template(#button-content)
                span.text-white {{getLang().toString().toUpperCase()}}
              b-dropdown-item-button(v-for="l in langs" @click="setLang(l)") {{l.toString().toUpperCase()}}
            //b-nav-item-dropdown(right text="Color" menu-class="color-dropdown")
              template(#button-content)
                span.fas.fa-circle.fa-lg.color-picker.text-white
              .d-flex.flex-wrap
                b-dropdown-item-button(v-for="c in colors" @click="setThemeColor(c)"
                  :style="{'background-color':c}").color-dropdown-item
     
    b-container(tag="main" fluid).slidesCont.flex-grow-1
      b-row(align-v="center").slides.h-100.p-0.pt-3
        transition(name="_slide" mode="out-in")
          component(:is="currSlide")
        
</template>

<script>
import {Lang} from './locale.js';
import AboutSlide from './components/AboutSlide.vue';
import ResumeSlide from './components/ResumeSlide.vue';
import PortfolioSlide from './components/PortfolioSlide.vue';
export default {
  name: 'app',
  components: {
    PortfolioSlide,
    AboutSlide,
    ResumeSlide
  },
  data(){
    return {
      slides: Lang.slides,
      colors: [
        "#E74C3C","#8E44AD","#3498DB","#16A085","#2ECC71","#F39C12"
      ],
      currSlide: ""
    }
  },
  created(){
    //this.setSlide(this.slides[0]);
    switch(window.location.hash){
      case "#resume":
        this.currSlide = "resume-slide";
        break;
      case "#portfolio":
        this.currSlide = "portfolio-slide";
        break;
      default:
        this.currSlide = "about-slide";
        break;
    }
  },
  mounted(){
    this.setThemeColor(this.colors[Math.floor(Math.random()*this.colors.length)]);
  },
  methods:{
    setSlide(s){
      this.currSlide = s+"-slide";
    },
    setThemeColor(col){
      this.$el.style.setProperty('--themeColor',col);
    },
    /* setLang(l){
      Lang.SetLang(l);
      console.log("lang set "+l)
    },
    getLang(){
      return Lang.GetLang();
    }, */
  }
}
</script>

<style lang="scss">
//$enable-responsive-font-sizes: true;

@import '~bootstrap';
@import '~bootstrap-vue';
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/fontawesome.min.css');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/solid.min.css');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/brands.min.css');

@include media-breakpoint-up(sm) { 
  .photoAbout{
    margin-bottom: -50px;
  }
}

#app{
  --themeColor: #ffffff;
}
#appWrapper{
  position: relative;
  overflow: auto;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
}
#pageHeadNav{
  position: sticky;
  top: 0;
  z-index: 3;
}
.themer{
  
  .theme-bg{
    //transition-duration: 0.3s;
    background-color: var(--themeColor);
  }
  .theme-color{
    color: var(--themeColor);
  }
  .theme-border{
    border-color: var(--themeColor);
  }
}
header{
  box-shadow: 0 0 10px #aaaaaa;
}
.navCont, .slide{
  max-width: 900px;
}
.header-btn{
  transition-duration: 0.3s;
  border-bottom: 1px solid #fff;
  //border-top: 1px solid #fff;
  &:hover{
    color: #888888 !important;
    box-shadow: 0 -8px 6px -6px rgba(255,255,255,0.5) inset;
    //border: 1px solid #fff;
  }
}
.color-dropdown{
  padding: 5px !important;
  box-sizing: content-box;
  min-width: 100px !important;
  width: 132px;
  height: 92px;
  >*{
    justify-content: space-around;
    align-content: space-around;
    width: 100%;
    height: 100%;
  }
  .color-dropdown-item{
    width: 40px;
    height: 40px;
    border: none;
    padding: 0;
    >*{
      width: 100%;
      height: 100%;
      padding: 0;
      &:hover{
        background-color: rgba(224, 224, 224, 0.4);
      }
    }
  }
}
._slide-enter-active,._slide-leave-active{
  transition: all 0.3s ease-out;
}
._slide-enter,._slide-leave-to{
  opacity: 0;
}
</style>
